import React, { Component } from "react";
import { Link } from "react-router-dom";
import ClockO from "react-icons/lib/fa/clock-o";

import Card from "../ui/Card";

import format from "date-fns/format";
import "./Launch.css";

class Launch extends Component {
  mapMissions = mis => {
    return (
      <span className="launch-mission-list">
        {mis.length === 0 ? this.props.data.name : mis.map(m => m.name)}
      </span>
    );
  };

  mapDescription = mis => {
    return mis.map(m => m.description).join(" ");
  };

  mapLinks = vidURLs => {
    return vidURLs.map(url => {
      let title = "Info";
      if (url.indexOf("youtube") > -1) {
        title = "Youtube stream";
      }
      return (
        <a className="launch-link" href={url}>
          {title}
        </a>
      );
    });
  };
  render() {
    const launchDate = new Date(this.props.data.net);
    const { id, rocket, missions } = this.props.data;
    return (
      <Link to={`/launch/${id}`}>
        <Card clickable>
          <div className="Launch-rocket-name">{rocket.name}</div>
          {missions[0] && (
            <div className="Launch-mission">{missions[0].name}</div>
          )}
          <div className="Launch-when">
            <ClockO />
            {format(launchDate, "D MMMM HH:mm")}
          </div>
          <p>{this.mapDescription(missions)}</p>
          <div style={{ textAlign: "right" }} />
        </Card>
      </Link>
    );
  }
}

export default Launch;



