import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import Launch from "./Launch";
import logo from "../logo.png";

import Preloader from "../ui/Preloader";

class LaunchesPage extends Component {
  renderLaunches = () => {
    return this.props.launches.map(launch => (
      <Launch key={launch.id} data={launch} />
    ));
  };
  render() {
    return (
      <div>
        <div className="welcome">
          <div className="welcome-content">
            <div className="welcome-logo">
              <Link to="/">
                <img
                  alt="logo"
                  src={logo}
                  height="24"
                  style={{ verticalAlign: "middle" }}
                />{" "}
                Liftoff
              </Link>
            </div>
            <div className="welcome-title">Rocket Launches</div>
          </div>
        </div>
        <div className="container">
          {this.renderLaunches()}
          {this.props.launches.length ? "" : <Preloader />}
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ data: { launches } }) => ({
  launches
});

export default connect(mapStateToProps)(LaunchesPage);



